// @flow
import React from 'react';
import classNames from 'classnames';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import TableOperation from 'core/table_operation';
import type { TableOperations } from 'lib/common/types';
import actions from '../actions';

type Props = {
  operation: TableOperation,
  index: number,
  changeOperation: (payload: { index: number, newOperation: TableOperation}) => void,
  availableOperations: TableOperations
}

type State = {
}

class OperationButton extends React.Component<Props, State> {
  operation: TableOperation
  handleOperationChange: (newOperation: TableOperation) => void
  renderOperation: any

  constructor(props: Props) {
    super(props);
    this.operation = props.operation;
    this.handleOperationChange = this.handleOperationChange.bind(this);
    this.renderOperation = this.renderOperation.bind(this);
  }

  handleOperationChange(newOperation: TableOperation): void {
    const { changeOperation, index } = this.props;

    changeOperation({ index, newOperation });
  }

  renderOperation(op: TableOperation) {
    const { operation } = this.props;
    return (
      <button
        type="button"
        className={classNames({
          'operation-button': true,
          reset: true,
          'operation-button-active': operation.name === op.name,
        })}
        onClick={() => this.handleOperationChange(op)}
        key={op.name}
      >
        {op.name}
      </button>
    );
  }

  render() {
    const { availableOperations } = this.props;

    return (
      <div className="available-operations">
        {availableOperations.map<React$Node>(this.renderOperation)}
      </div>
    );
  }
}

function mapDispatchToProps(dispatch) {
  return bindActionCreators({
    changeOperation: actions.changeOperation,
  }, dispatch);
}

export default connect(null, mapDispatchToProps)(OperationButton);
