// @flow
import React from 'react';
import Modal from 'react-modal';
import Table from 'core/table';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import TablePreview from './table_preview';
import actions from '../actions';

type Props = {
  addTable: (table: Table) => void,
  addNextPossibleOperation: () => void
}

type State = {
  table?: null | Table
}

class UploadButton extends React.Component<Props, State> {
  static resetFileInput(e: SyntheticInputEvent<HTMLInputElement>) {
    e.target.value = '';
  }

  handleFile: (e: SyntheticInputEvent<HTMLInputElement>) => void
  handleAddTable: () => void
  handleCancel: () => void

  constructor(props: Props) {
    super(props);

    this.state = {
      table: null,
    };

    this.handleFile = this.handleFile.bind(this);
    this.handleCancel = this.handleCancel.bind(this);
    this.handleAddTable = this.handleAddTable.bind(this);
  }
  handleCancel() {
    this.setState({ table: null });
  }

  handleFile(e: SyntheticInputEvent<HTMLInputElement>) {
    e.preventDefault();
    const reader = new FileReader();
    const file = e.target.files[0];
    const { name } = file;
    reader.onloadend = () => {
      const table = Table.fromData({ data: reader.result, name });
      this.setState({ table });
    };
    reader.readAsBinaryString(file);
  }

  handleAddTable() {
    const { table } = this.state;

    this.setState({ table: null }, () => {
      if (table) {
        const {
          addTable, addNextPossibleOperation,
        } = this.props;

        addTable(table);
        addNextPossibleOperation();
      }
    });
  }

  renderInputButton() {
    return <div className="file">
      <label className="file-label">
        <input className="file-input" type="file" name="resume" onChange={this.handleFile} onClick={UploadButton.resetFileInput} />
        <span className="file-cta">
          <span className="file-icon">
            <svg class="svg-inline--fa fa-upload fa-w-16" aria-hidden="true" focusable="false" data-prefix="fas" data-icon="upload" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" data-fa-i2svg="">
              <path fill="currentColor" d="M296 384h-80c-13.3 0-24-10.7-24-24V192h-87.7c-17.8 0-26.7-21.5-14.1-34.1L242.3 5.7c7.5-7.5 19.8-7.5 27.3 0l152.2 152.2c12.6 12.6 3.7 34.1-14.1 34.1H320v168c0 13.3-10.7 24-24 24zm216-8v112c0 13.3-10.7 24-24 24H24c-13.3 0-24-10.7-24-24V376c0-13.3 10.7-24 24-24h136v8c0 30.9 25.1 56 56 56h80c30.9 0 56-25.1 56-56v-8h136c13.3 0 24 10.7 24 24zm-124 88c0-11-9-20-20-20s-20 9-20 20 9 20 20 20 20-9 20-20zm64 0c0-11-9-20-20-20s-20 9-20 20 9 20 20 20 20-9 20-20z" />
            </svg>
          </span>
          <span className="file-label">
            Upload a new table
          </span>
        </span>
      </label>
    </div>
  }

  render() {
    const { table } = this.state;

    return (
      <>
        {this.renderInputButton()}
        {
          table
          && (
          <Modal isOpen>
            <TablePreview
              table={table}
              buttons={[
                { title: 'Cancel', callback: this.handleCancel, classes: ['button', 'cancel'] },
                { title: 'Add Table', callback: this.handleAddTable, classes: ['button', 'add-table', 'is-primary'], autoFocus: true },
              ]}
            />
          </Modal>
          )
        }
      </>
    );
  }
}


function mapDispatchToProps(dispatch) {
  return bindActionCreators({
    addTable: actions.addTable,
    addOperation: actions.addOperation,
    addNextPossibleOperation: actions.addNextPossibleOperation,
  }, dispatch);
}

export default connect(null, mapDispatchToProps)(UploadButton);
