//
// Cool little typesafe DSL for action creators.
//
// Example:
//   const Actions = {
//     loadData: {
//       request: action( "LOAD_DATA_REQUEST" ).empty(),
//       success: action( "LOAD_DATA_SUCCESS" ).withPayload<{ name: string }>(),
//       failure: action( "LOAD_DATA_FAILURE" ).withError()
//     }
//   }
//
export class ActionFactory {
  constructor(type) {
    this.type = type;
  }

  //
  // Creates action creator with no payload.
  //
  empty() {
    const ac = () => ({ type: this.type });

    return Object.assign(ac, {
      getType: () => this.type,
      toString: () => this.type.toString(),
    });
  }

  //
  // Creates action creator with payload.
  //
  withPayload() {
    const ac = (payload) => ({ type: this.type, payload });

    return Object.assign(ac, {
      getType: () => this.type,
      toString: () => this.type.toString(),
    });
  }

  //
  // Creates action creator with error as a payload.
  //
  withError() {
    const ac = (payload) => ({ type: this.type, payload, error: true });

    return Object.assign(ac, {
      getType: () => this.type,
      toString: () => this.type.toString(),
    });
  }
}

export function action(type) {
  return new ActionFactory(type);
}
