// @flow
import React from 'react';
import { connect } from 'react-redux';
import type { Tables, TableOperations } from 'lib/common/types';
import type Table from 'core/table';
import { TABLE_OPERATOR_INFO, TABLE, OPERATOR } from 'core/constants';
import ShuntingYard from 'core/shunting_yard';
import TableOperation from 'core/table_operation';
import { getTables, getOperations } from '../selectors';
import OperationButton from './operation_button';
import TableCard from './table_card';

type Props = {
  tables: Tables,
  operations: TableOperations,
}

type State = {

}

class ButtonBoard extends React.Component<Props, State> {
  static operationCard(operation: TableOperation) {
    return (
      <p key={operation.name}>
        {operation.name}
      </p>
    );
  }
  shuntingYard: ShuntingYard;

  constructor(props: Props) {
    super(props);
    this.state = { };

    this.shuntingYard = new ShuntingYard(TABLE_OPERATOR_INFO);
  }

  shouldShowResult(): boolean {
    const { operations } = this.props;
    return TableOperation.lastOperationIsTerminal(operations);
  }

  calculateResult(): Table {
    const { tables, operations } = this.props;
    const operands = [];
    tables.forEach((table, index) => {
      operands.push({ type: TABLE, value: table });
      operands.push({ type: OPERATOR, value: operations[index].name });
    });

    // remove the equals operation from the end
    operands.pop();
    return this.shuntingYard.solve(operands);
  }

  render() {
    const { tables, operations } = this.props;
    // Render table + operation + upload button
    return (
      <section className="buttonBoard">
        {
          tables.map((table, index) => {
            const operation = operations[index];
            const availableOperations = TableOperation.availableOperations(
              tables,
              operations,
              index,
            );

            const operationNode = operation && (
              <OperationButton
                operation={operation}
                availableOperations={availableOperations}
                index={index}
                key={operation.name}
              />
            );

            return [<TableCard table={table} />, operationNode];
          })
        }
        {this.shouldShowResult() && <TableCard isResultTable table={this.calculateResult()} />}
      </section>
    );
  }
}


function mapStateToProps(state) {
  return {
    tables: getTables(state),
    operations: getOperations(state),
  };
}

export default connect(mapStateToProps, null)(ButtonBoard);
