// @flow
import XLSX from 'xlsx';
import tableOperations from 'core/table_operations';
import type {
  Column, Keys, Columns, Row, TableHash,
} from 'lib/common/types';
import TableSheet from 'core/table_sheet';

type FileReaderData = null | string | ArrayBuffer

type constructorType = { name: string, headers: boolean, sheets: Array<TableSheet> }
type fromDataType = { data: FileReaderData, name: string, headers?: boolean }

export default class Table {
  jsonCache: Object
  name: string
  headers: boolean
  sheetNames: Array<string>
  sheets: Array<TableSheet>
  keys: Keys
  currentSheetName: string
  currentSheet: Object
  columns: Columns
  plus: (table: Table) => Table
  minus: (table: Table) => Table

  constructor({ name, headers, sheets }: constructorType) {
    this.jsonCache = {};
    this.name = name;
    this.headers = headers;
    this.sheets = sheets;
    this.sheetNames = this.sheets.map((sheet) => sheet.name);

    this.setCurrentSheet(this.sheetNames[0]);
    this.setHeaders(headers);
    this.setKeys(this.guessKeys());

    Object.assign(this, tableOperations);
  }

  static fromData({ data, name, headers = true }: fromDataType) {
    const tableObject = this.parse(data);
    const sheets = Object.entries(tableObject.Sheets).map(
      ([shetName, sheet]) => new TableSheet(shetName, sheet, headers, true),
    );
    const tableHeaders = headers || true;

    return new Table({ name, headers: tableHeaders, sheets });
  }

  static parse(data: FileReaderData) {
    const type = typeof (data) === 'string' ? 'binary' : 'array';
    return XLSX.read(data, { type });
  }

  toWorkBook() {
    const ws = XLSX.utils.json_to_sheet(this.rows);
    const wb = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(wb, ws, 'Sheet 1');

    return XLSX.writeFile(wb, 'sheetjs.xlsx');
  }

  /**
   * Calculate the hashes for each row of the table
   *
   * @returns Object in a shape of { "hashKey": Array }
   * @readonly
   * @memberof Table
   */
  get tableHash(): TableHash {
    const hashedTable = {};
    this.rows.forEach((row) => {
      const hashKey = this.rowHash(row);
      hashedTable[hashKey] = row;
    });
    return hashedTable;
  }

  /**
    * Calculates the hash for the row.
    * The hash is a concatenated values of the columns that are marked as keys
    *
    * @param {*} row Array or Object
    * @returns string
    * @memberof Table
    */
  rowHash(row: Object | Array<any>): Object {
    const keyPart : string | number = this.headers ? 'column' : 'index';
    return this.keys.map((key) => row[key[keyPart]]).sort().join();
  }

  get columns(): Columns {
    if (this.headers) {
      return Object.keys(this.firstRow).map((column: string, index: number) => ({ column, index }));
    }
    return Object.keys(this.firstRow).map((column: string) => (
      { column, index: parseInt(column, 10) }: Column
    ));
  }

  get columnNames(): string[] {
    return this.columns.map(({ column }) => column);
  }

  get rows() {
    return this.currentSheet.rows;
  }

  get firstRow(): Row {
    const blankValue = this.headers ? { } : [];
    return this.rows[0] || blankValue;
  }

  // Keys are pairs { index: 0, column: "0"}
  setKeys(keys: Keys): Table {
    this.keys = keys;
    return this;
  }

  setHeaders(headers: boolean): Table {
    this.headers = headers;
    this.sheets.forEach((sheet) => sheet.setHeaders(headers));
    this.setKeys(this.guessKeys());
    return this;
  }

  setCurrentSheet(sheetName: string) {
    this.currentSheetName = sheetName;
    this.currentSheet = this.sheets.find((sheet) => sheet.name === sheetName);
    this.setKeys(this.guessKeys());
    return this;
  }

  // The usual uniqueness criterion for the tables is the email column
  // thus we either look for the header column that looks like email
  // or we examine each value of the first row and see if it looks like email
  guessKeys(): Keys {
    let key = null;

    if (this.headers) {
      key = this.columns.find(({ column }) => Table.looksLikeEmailField(column));
    } else {
      const emailField = this.firstRow.find((el) => Table.looksLikeEmail(el));
      if (emailField) {
        key = {
          column: this.firstRow.indexOf(emailField).toString(),
          index: this.firstRow.indexOf(emailField),
        };
      }
    }
    key = key || this.columns[0];

    if (key === undefined) {
      return [];
    }

    return [key];
  }

  static looksLikeEmailField(column: string | Array<any>) {
    if (typeof (column) !== 'string') { return false; }
    return /email/i.test(column);
  }

  static looksLikeEmail(column: string | any) {
    if (typeof (column) !== 'string') { return false; }
    return /[^@]+@[^@]+\.[^@]+/i.test(column);
  }
}
