// @flow
import type {
  InitialStateType, Tables, TableOperations,
} from 'lib/common/types';
import TableOperation from 'core/table_operation';
import actions from './actions';

const INITIAL_STATE = {
  tables: [],
  operations: [],
};

export default (state: InitialStateType = INITIAL_STATE, action: actions) => {
  switch (action.type) {
    case actions.addTable.getType(): {
      const tables: Tables = state.tables.concat(action.payload);

      return {
        ...state,
        tables,
      };
    }

    case actions.deleteTable.getType(): {
      const tables: Tables = state.tables.filter((table) => table !== action.payload);

      return {
        ...state,
        tables,
      };
    }

    case actions.changeOperation.getType(): {
      const { index, newOperation } = action.payload;

      const operations = state.operations.map<any>((operation, i) => (
        i === index ? newOperation : operation
      ));

      return {
        ...state,
        operations,
      };
    }

    case actions.addOperation.getType(): {
      const operation: TableOperation = action.payload;
      const operations: TableOperations = state.operations.concat(operation);

      return {
        ...state,
        operations,
      };
    }

    case actions.deleteOperation.getType(): {
      const operation: TableOperation = action.payload;
      const operations: TableOperations = state.operations.filter((op) => (
        op !== operation
      ));

      return {
        ...state,
        operations,
      };
    }

    case actions.addNextPossibleOperation.getType(): {
      const { tables, operations } = state;

      const nextPossOp: TableOperation | null = TableOperation.nextPossibleOperation(
        tables,
        operations,
      );

      const newOps: TableOperations = nextPossOp ? operations.concat(nextPossOp) : operations;

      return {
        ...state,
        operations: newOps,
      };
    }

    case actions.updateOperations.getType(): {
      const { tables, operations } = state;
      const { deletedIndex } = action.payload;
      const newOps = TableOperation.calculateOperations(tables, operations, deletedIndex);

      return {
        ...state,
        operations: newOps,
      };
    }

    default:
      return state;
  }
};
