// @flow
import React from 'react';
import { connect } from 'react-redux';
import Modal from 'react-modal';
import type { Tables, TableOperations } from 'lib/common/types';
import UploadButton from './upload_button';
import ButtonBoard from './button_board';

// Fixtures
// eslint-disable-next-line import/order
import Table from 'core/table';
// eslint-disable-next-line import/order
import { bindActionCreators } from 'redux';
import actions from '../actions';
import * as fixtureTables from './fixtures';
import { decode } from './array_buffer_base64';
// eslint-disable-next-line import/order
import TableOperation from 'core/table_operation';
import { getTables, getOperations } from '../selectors';

function xlsxTable(name: string = 'test.xlsx') {
  return Table.fromData({ data: decode(fixtureTables.xlsxTable2), name });
}
//--------------
type Props = {
  addTable: (table: Table) => void,
  addOperation: (operation: TableOperation) => void,
  tables: Tables,
  operations: TableOperations,
}

type State = {}

class Container extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props);
    // --------Fixtures------
    const { addTable, addOperation } = props;
    const table1: Table = xlsxTable();
    const table2: Table = xlsxTable('test2.xlsx');
    addTable(table1);
    addOperation(TableOperation.plus());
    addTable(table2.setCurrentSheet(table2.sheetNames[2]));
    addOperation(TableOperation.equals());
    // ----------------------
  }

  componentDidMount() {
    Modal.setAppElement('body');
  }

  canUpload(): boolean {
    const { tables, operations } = this.props;
    // There are no tables => yes
    if (tables.length === 0) { return true; }
    if (TableOperation.lastOperationIsTerminal(operations)) { return false; }

    return true;
  }

  render() {
    return (
      <>
        <ButtonBoard />
        { this.canUpload() && <UploadButton /> }
      </>
    );
  }
}

// Fixtures
function mapDispatchToProps(dispatch) {
  return bindActionCreators({
    addTable: actions.addTable,
    addOperation: actions.addOperation,
  }, dispatch);
}

function mapStateToProps(state) {
  return {
    tables: getTables(state),
    operations: getOperations(state),
  };
}
// -------------------

export default connect(mapStateToProps, mapDispatchToProps)(Container);
