import React from 'react';
import './App.css';
import UIContext from './lib/UIContext';
import * as TableButtons from './modules/table_buttons';

// Init window.APP namespace
if (!global.APP) {
  global.APP = {};
}

// Init UI context
global.APP.UI = new UIContext();
global.APP.UI.registerModule(TableButtons);

function App() {
  const TableButtonsComponent = global.APP.UI.getConnectedComponent('table_buttons');

  return (
    <div className="App">
      <h1>Table calculator</h1>
      <p>
        This program helps
        &nbsp;
        <div className="tooltip">
          my beloved wife
          <div className="bottom">
            <img src="./small_inna.jpg" alt="Инночка-картиночка" />
            <i />
          </div>
        </div>
        &nbsp;
        to perform basic operations on Excel tables
      </p>

      <TableButtonsComponent />
    </div>
  );
}

export default App;
