import React from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import Modal from 'react-modal';
import type Table from 'core/table';
import classnames from 'classnames';
import type { Tables } from 'lib/common/types';
import { getTables, getOperations } from '../selectors';
import actions from '../actions';
import TablePreview from './table_preview';

type Props = {
  table: Table,
  tables: Tables,
  deleteTable: (table: Table) => void,
  updateOperations: (index: number) => void,
  isResultTable: boolean
}

type State = {
  isOpen: boolean
}

class TableCard extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props);

    this.state = { previewIsOpen: false };
    this.handleDelete = this.handleDelete.bind(this);
    this.togglePreview = this.togglePreview.bind(this);
    this.handleDownload = this.handleDownload.bind(this);
  }

  handleDelete(): void {
    const {
      deleteTable, updateOperations, tables, table,
    } = this.props;
    const deletedIndex = tables.indexOf(table);

    deleteTable(table);
    updateOperations({ deletedIndex });
  }

  handleDownload(): void {
    const { table } = this.props;
    const result = table.toWorkBook();
    return result;
  }

  togglePreview(): void {
    const { previewIsOpen } = this.state;
    this.setState({ previewIsOpen: !previewIsOpen });
  }

  render() {
    const { table, isResultTable } = this.props;
    const { previewIsOpen } = this.state;

    return (
      <div
        className={classnames({
          'table-button': true,
          result: isResultTable,
        })}
        key={`${table.name}${table.currentSheetName}`}
      >
        <div
          className="table-info ellipsis"
          role="button"
          tabIndex={0}
          onClick={this.togglePreview}
          onKeyPress={this.togglePreview}
        >
          <span className="table-filename" title={table.name}>{table.name}</span>
          <br />
          <span className="table-sheetname" title={table.currentSheetName}>{table.currentSheetName}</span>
        </div>
        <button
          type="button"
          className={`table-delete reset ${(isResultTable && 'download') || ''}`}
          title={isResultTable ? 'Download result' : 'Delete the table'}
          onClick={isResultTable ? this.handleDownload : this.handleDelete}
        >
          {!isResultTable && '✕' }
        </button>
        <ul className="table-keys-list">
          {table.keys.map((key) => <li key={key.index} className="table-keys-list-item ellipsis">{key.column}</li>)}
        </ul>
        <Modal isOpen={previewIsOpen}>
          <TablePreview
            table={table}
            buttons={[
              { title: 'Cancel', callback: this.togglePreview, classes: ['button', 'cancel'] },
              { title: 'Download', callback: this.handleDownload, classes: ['button', 'add-table', 'is-primary'] },
            ]}
          />
        </Modal>
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    tables: getTables(state),
    operations: getOperations(state),
  };
}
function mapDispatchToProps(dispatch) {
  return bindActionCreators({
    deleteTable: actions.deleteTable,
    updateOperations: actions.updateOperations,
  }, dispatch);
}
export default connect(mapStateToProps, mapDispatchToProps)(TableCard);
