import { action } from '../../lib/actions';

const ADD_TABLE = 'ADD_TABLE';
const ADD_OPERATION = 'ADD_OPERATION';
const DELETE_TABLE = 'DELETE_TABLE';
const CHANGE_OPERATION = 'CHANGE_OPERATION';
const DELETE_OPERATION = 'DELETE_OPERATION';
const ADD_NEXT_POSSIBLE_OPERATION = 'ADD_NEXT_POSSIBLE_OPERATION';
const UPDATE_OPERATIONS = 'UPDATE_OPERATIONS';

export default {
  addTable: action(ADD_TABLE).withPayload(),
  addOperation: action(ADD_OPERATION).withPayload(),
  deleteTable: action(DELETE_TABLE).withPayload(),
  deleteOperation: action(DELETE_OPERATION).withPayload(),
  changeOperation: action(CHANGE_OPERATION).withPayload(),
  addNextPossibleOperation: action(ADD_NEXT_POSSIBLE_OPERATION).withPayload(),
  updateOperations: action(UPDATE_OPERATIONS).withPayload(),
};
