import { forOwn } from 'lodash';
import { createBrowserHistory } from 'history';
import createStore from '../store/createStore';
import { withUIContext } from './withUIContext';

//
// Global UI context for the page. Contains all global state and registered components.
//
export default class UIContext {
  // List of registered modules
  modules = {}

  // List of registered React component
  components = {}

  // Browser history wrapper
  history = createBrowserHistory()

  // Redux store
  store = createStore(this)

  //
  // Registers new component with given name.
  //
  registerComponent(name, component) {
    this.components[name] = component;
  }

  //
  // Returns registered component by name.
  //
  getComponent(name) {
    return this.components[name];
  }

  //
  // Returns registered component by name.
  //
  getConnectedComponent(name) {
    const component = this.getComponent(name);
    return component ? withUIContext(this, component) : null;
  }

  //
  // Returns registered module by name.
  //
  getModule(name) {
    return this.modules[name];
  }

  //
  // Registers new module.
  //
  registerModule(module) {
    // Inject module reducer
    if (module.reducer) {
      this.store.injectReducer(module.MODULE_NAME, module.reducer);
    }

    // Run module saga
    if (module.saga) {
      this.store.runSaga(module.saga);
    }

    // Register components wihin module namespace
    forOwn(module.components, (component, componentName) => {
      this.registerComponent(`${module.MODULE_NAME}:${componentName}`, component);
    });

    // Register default component
    if (module.container) {
      this.registerComponent(module.MODULE_NAME, module.container);
    }
  }
}
