// @flow
import type { Tables, TableOperations } from 'lib/common/types';

class TableOperation {
  name: string
  boundOperation: string
  static _plus: TableOperation
  static _minus: TableOperation
  static _equals: TableOperation

  constructor({ name, boundOperation }: { name: string, boundOperation: string}) {
    this.name = name;
    this.boundOperation = boundOperation;
  }

  /**
   * This function answers the question what is the next possible operation.
   * The algorithm is quite simple:
   * - prefer a plus operation if there is only one table on the board
   * - prefer the default operation if there are more operations available
   *
   * @static
   * @param {Tables} tables
   * @param {TableOperations} operations
   * @returns {(TableOperation | null)}
   * @memberof TableOperation
   */
  static nextPossibleOperation(tables: Tables, operations: TableOperations): TableOperation | null {
    if (tables.length === 0) { return null; }
    if (tables.length === 1) {
      if (operations.length === 0) { return TableOperation.defaultOperation(); }
      if (operations.length > 1) { return null; }
    }

    // If the last operation is equals, no more operations available
    if (operations.length > 0 && operations[operations.length - 1] === TableOperation.equals) {
      return null;
    }

    return TableOperation.defaultOperation();
  }

  /**
   * This function is used to define if the last operation is 'equals'. This is helpful
   * to determine whether to show an upload button or not.
   *
   * @static
   * @param {TableOperations} operations list of all operations
   * @returns {boolean}
   * @memberof TableOperation
   */
  static lastOperationIsTerminal(operations: TableOperations): boolean {
    const lastOperation = operations[operations.length - 1];
    // Last operation is equals => no
    return (lastOperation && lastOperation === TableOperation.equals());
  }


  /**
   * This function helps to recalculate operations after deletion of a table.
   * When the table gets deleted:
   * - Delete the operation after a table
   * - Recalculate the last operation:
   *   - the last operation is equals and there's a single table on the board -> return default one
   *
   * @static
   * @param {Tables} tables list of all tables
   * @param {TableOperations} operations list of all operations
   * @param {number} deletedIndex the index of the removed table
   * @returns {TableOperations} truncated list of operations
   * @memberof TableOperation
   */
  // eslint-disable-next-line max-len
  static calculateOperations(
    tables: Tables,
    operations: TableOperations,
    deletedIndex: number,
  ): TableOperations {
    const newOperations = operations.filter((operation, index) => index !== deletedIndex);

    if (tables.length === 0) { return []; }
    if (tables.length === 1 && operations.length > 0) {
      return newOperations.map((operation, index) => {
        if (index === newOperations.length - 1 && operation === TableOperation.equals()) {
          return TableOperation.defaultOperation();
        }
        return operation;
      });
    }

    return newOperations;
  }

  /**
   * Basically, only two operations should be always available -> plus and minus
   * The last operation button in the stack should have equals too
   * The only operation button cannot have equals operation
   *
   * @static
   * @param {Tables} tables
   * @param {TableOperations} operations
   * @param {number} operationIndex
   * @returns {TableOperations}
   * @memberof TableOperation
   */
  static availableOperations(
    tables: Tables,
    operations: TableOperations,
    operationIndex: number,
  ): TableOperations {
    const avaialbleOperations: TableOperations = [TableOperation.plus(), TableOperation.minus()];
    if (operations.length === 0) { return []; }
    if (operations.length === 1) {
      return avaialbleOperations;
    }

    if ((operations.length - 1) === operationIndex) {
      return avaialbleOperations.concat(TableOperation.equals());
    }

    return avaialbleOperations;
  }

  static plus(): TableOperation {
    // eslint-disable-next-line no-underscore-dangle
    this._plus = this._plus || new TableOperation({ name: '+', boundOperation: 'plus' });
    // eslint-disable-next-line no-underscore-dangle
    return this._plus;
  }

  static minus(): TableOperation {
    // eslint-disable-next-line no-underscore-dangle
    this._minus = this._minus || new TableOperation({ name: '–', boundOperation: 'minus' });
    // eslint-disable-next-line no-underscore-dangle
    return this._minus;
  }

  static equals(): TableOperation {
    // eslint-disable-next-line no-underscore-dangle
    this._equals = this._equals || new TableOperation({ name: '=', boundOperation: 'equals' });
    // eslint-disable-next-line no-underscore-dangle
    return this._equals;
  }

  static defaultOperation(): TableOperation {
    return TableOperation.plus();
  }

  static allOperations(): Object {
    return {
      plus: this.plus,
      minus: this.plus,
      equals: this.plus,
    };
  }
}

export default TableOperation;
