// @flow
import Table from 'core/table';
import TableSheet from 'core/table_sheet';
import type Columns from 'lib/common/types';

// to subtract one table form another
// 1. Create a hash for the keys for the first table
// 2. Create a hash for the keys for the second table
// 3. Walk though the keys of the first table
//   3.1 Do not include the key in the result if it presents in the hash of the second table
// 4. Returns artificial result table which acts almost like a Table
function minus(table2: Table): Table {
  const table1Hash = this.tableHash;
  const table2Hash = table2.tableHash;

  const rows = Object.keys(table1Hash).filter((hash) => !table2Hash[hash])
    .map((hash) => table1Hash[hash]);

  const sheet = new TableSheet('Sheet1', rows, this.headers, false);

  return new Table({
    name: 'Result',
    headers: this.headers,
    sheets: [sheet],
  });
}

function plus(table2: Table): Table {
  const table1Hash = this.tableHash;
  const table2Hash = table2.tableHash;
  const rows = [];

  // First, let's gather all the keys from both tables
  const keys = this.columnNames.concat(table2.columnNames);
  // Then let's create an object that consists of the keys with null values
  const dummyObject = Object.fromEntries(keys.map((key) => [key, null]));

  Object.keys(table1Hash).forEach((hash) => {
    const table1Data = table1Hash[hash];
    const table2Data = table2Hash[hash];
    // If there's no such record in the second table then add existing one to the result
    if (!table2Data) {
      // Override the dummy object with table1Data
      rows.push({ ...dummyObject, ...table1Data });
    } else if (this.headers && table2.headers) {
      // Merge both rows, preferring the data from the first table owerwriting keys in the second
      rows.push({ ...table2Data, ...table1Data });
    }
  });

  Object.keys(table2Hash).filter((hash) => !table1Hash[hash]).forEach((hash) => {
    if (this.headers && table2.headers) {
      const data: any = table2Hash[hash];
      rows.push({ ...dummyObject, ...data });
    }
  });

  const headers = this.headers || table2.headers;
  const sheet = new TableSheet('Sheet1', rows, headers, false);

  return new Table({
    name: 'Result',
    sheets: [sheet],
    headers,
  });
}


export default {
  minus,
  plus,
};
