// @flow
/* eslint-disable max-len */
import type { OperatorInfo, Token, Operator } from 'core/types';
import {
  OPERANDS,
  FUNCTION,
  OPERATOR,
  OPEN_BRACKET,
  CLOSING_BRACKET,
  LIST_START,
  LEFT_ASSOCIATIVITY,
} from 'core/constants';

class ShuntingYard {
  operatorInfo: OperatorInfo

  constructor(operatorInfo: OperatorInfo) {
    this.operatorInfo = operatorInfo;
  }

  solve(tokenized: Array<Token>) {
    const rpn = this.shuntingYard(tokenized);
    return this.solveRPN(rpn);
  }

  shuntingYard(tokenized: Array<Token>):Array<Token> {
    const outputQueue = [];
    const operatorStack = [];

    tokenized.forEach((token) => {
      const { type } = token;

      // if the token is an operand, then:
      //   push it to the output queue.
      if (OPERANDS.includes(type)) {
        outputQueue.push(token);
      }

      // if the token is a function then:
      //   push it onto the operator stack
      if (type === FUNCTION) {
        operatorStack.push(token);
      }

      // if the token is an operator, then:
      //   while (
      //     (there is a function at the top of the operator stack)
      //     or (there is an operator at the top of the operator stack with greater precedence)
      //     or (the operator at the top of the operator stack has equal precedence and is left associative)
      //   )
      //   and (the operator at the top of the operator stack is not a left parenthesis):
      //     pop operators from the operator stack onto the output queue.
      //   push it onto the operator stack.
      if (type === OPERATOR) {
        const { precedence } = this.operatorInfo[token.value.toString()];

        let operatorStackTop = operatorStack[operatorStack.length - 1];
        let operatorStackTopInfo: Operator = operatorStackTop && this.operatorInfo[operatorStackTop.value.toString()];

        while (
          operatorStackTop
          && (
            (
              (
                operatorStackTop.type === FUNCTION
              ) || (
                (operatorStackTop.type === OPERATOR)
                && (
                  (operatorStackTopInfo.precedence > precedence)
                  || (
                    (operatorStackTopInfo.precedence === precedence)
                    && (operatorStackTopInfo.associativity === LEFT_ASSOCIATIVITY)
                  )
                )
              )
            ) && (
              operatorStackTop.type !== OPEN_BRACKET
            )
          )) {
          outputQueue.push(operatorStack.pop());
          operatorStackTop = operatorStack[operatorStack.length - 1];
          operatorStackTopInfo = operatorStackTop && this.operatorInfo[operatorStackTop.value.toString()];
        }
        operatorStack.push(token);
      }

      // if the token is a left paren (i.e. "("), then:
      //   push it onto the operator stack.
      if (type === OPEN_BRACKET) {
        // ---- Extension ----
        // If there is a function token at the top of the stack, add a left parenthesis (LIST_START) to the output queue

        const topOperator = operatorStack[operatorStack.length - 1];
        if (topOperator && topOperator.type === FUNCTION) {
          outputQueue.push(token);
        } else {
          operatorStack.push(token);
        }
        // -------------------
        // original:
        // operatorStack.push(token);
      }

      // if the token is a right paren (i.e. ")"), then:
      //   while the operator at the top of the operator stack is not a left paren (extension: or function):
      //     pop the operator from the operator stack onto the output queue.
      //   /* if the stack runs out without finding a left paren, then there are mismatched parentheses. */
      //   if there is a left paren at the top of the operator stack, then:
      //     pop the operator from the operator stack and discard it
      //   Extension:
      //   if there is a function at the top of the operator stack, then:
      //     push function token operator stack to the ouptut queue
      if (type === CLOSING_BRACKET) {
        let topOperator = operatorStack[operatorStack.length - 1];

        while (topOperator && topOperator.type !== OPEN_BRACKET && topOperator.type !== FUNCTION) {
          if (operatorStack.length === 0) {
            throw new Error('There\'re mismatch parentheses');
          }
          outputQueue.push(operatorStack.pop());
          topOperator = operatorStack[operatorStack.length - 1];
        }

        if (topOperator) {
          if (topOperator.type === OPEN_BRACKET) {
            operatorStack.pop();
          }
          if (topOperator.type === FUNCTION) {
            outputQueue.push(operatorStack.pop());
          }
        }
      }
    });

    /* After while loop, if operator stack not null, pop everything to output queue */
    while (operatorStack.length > 0) {
      outputQueue.push(operatorStack.pop());
    }

    return outputQueue;
  }

  solveRPN(rpn: Array<Token>) {
    const stack = [];

    rpn.forEach((token: Token) => {
      const { type, value } = token;

      if (OPERANDS.includes(type)) {
        stack.push(value);
      }

      if (type === OPERATOR) {
        const { arity, operation } = this.operatorInfo[value.toString()];
        const operands = [];
        for (let i = 0; i < arity; i += 1) {
          operands.push(stack.pop());
        }

        const args = operands.reverse();

        if (operation) {
          const result = operation(...args);
          stack.push(result);
        } else {
          throw new Error(`Please define operation on the ${type}`);
        }
      }

      // ---- Extension ----
      if (type === OPEN_BRACKET) {
        stack.push(value);
      }

      if (type === FUNCTION) {
        const functionArguments = [];

        let stackTop = stack[stack.length - 1];
        while (stackTop && stackTop !== LIST_START) {
          functionArguments.push(stack.pop());
          stackTop = stack[stack.length - 1];
        }
        if (stackTop === LIST_START) {
          stack.pop();
        }

        if (typeof (value) === 'function') {
          const result = value(...functionArguments);
          stack.push(result);
        } else {
          throw new Error(`${value} is not a function`);
        }
      }
      // -------------------
    });

    return stack[0];
  }
}

export default ShuntingYard;
