// @flow
import XLSX from 'xlsx';

export default class TableSheet {
  name: string
  data: Object
  fromFile: boolean
  headers: boolean
  cache: Object

  constructor(name: string, data: Object, headers:boolean = true, fromFile:boolean = false) {
    this.name = name;
    this.data = data;
    this.fromFile = fromFile;
    this.headers = headers;
    this.cache = {};
  }

  setHeaders(headers:boolean) {
    this.headers = headers;
  }

  get rows(): Object | Array<Array<Object>> {
    if (!this.fromFile) {
      return this.data;
    }

    const key = this.name + this.headers.toString();
    const options = this.headers ? {} : { header: 1 };

    this.cache[key] = this.cache[key] || XLSX.utils.sheet_to_json(this.data, options);

    return this.cache[key];
  }
}
