// @flow
import * as React from 'react';
import ReactDataGrid from 'react-data-grid';
import classNames from 'classnames';

import Table from 'core/table';
import type {
  Key, Column, Keys, ButtonInfo,
} from 'lib/common/types';

type Props = {
  table: Table,
  buttons: Array<ButtonInfo>
}

type State = {
  headers: boolean,
  keys: Keys
}

class TablePreview extends React.Component<Props, State> {
  static keyEqualToColumn(key: Key, column: Column): boolean {
    return key.column === column.column && key.index === column.index;
  }

  handleToggleKey: function
  setCurrentSheet: function
  handleHeaders: function

  constructor(props: Props) {
    super(props);
    const { table } = props;

    this.state = {
      headers: table.headers,
      keys: table.keys,
    };

    this.setCurrentSheet = this.setCurrentSheet.bind(this);
    this.handleHeaders = this.handleHeaders.bind(this);
    this.handleToggleKey = this.handleToggleKey.bind(this);
  }

  componentDidMount() {
    window.addEventListener('resize', this.handleResize);
  }

  setCurrentSheet(name: string) {
    const { table } = this.props;
    table.setCurrentSheet(name);

    this.setState({
      keys: table.keys,
    });
  }

  handleResize = () => {
    this.forceUpdate();
  }

  handleHeaders(e: SyntheticInputEvent<HTMLInputElement>): void {
    const turnedOn = e.target.checked;
    this.setState(() => {
      const { table } = this.props;

      table.setHeaders(turnedOn);
      return { headers: true };
    });
  }

  handleToggleKey(e: SyntheticInputEvent<HTMLInputElement>, column: Column): void {
    const { table } = this.props;
    const { keys } = table;
    let resultKeys = [];
    const turnedOn = e.target.checked;
    if (turnedOn) {
      resultKeys = keys.concat(column);
    } else {
      resultKeys = keys.filter((key) => !TablePreview.keyEqualToColumn(key, column));
    }

    table.setKeys(resultKeys);
    this.setState({ keys: resultKeys });
  }

  sheetTabs(): Object {
    const { table } = this.props;

    return (
      <div className="sheet-tabs">
        {table.sheetNames.map((sheetName: string) => (
          <button
            className={classNames({
              'sheet-tab': true,
              'sheet-tab-active': table.currentSheetName === sheetName,
            })}
            type="button"
            key={sheetName}
            onClick={() => this.setCurrentSheet(sheetName)}
          >
            <span>{sheetName}</span>
          </button>
        ))}
      </div>
    );
  }

  headerRenderer(data: Object, column: Column) {
    const { keys } = this.state;

    const headerText = data.column.rowType === 'header' ? data.column.name : '';
    const checked = keys.some((key) => TablePreview.keyEqualToColumn(key, column));

    return (
      <label className="checkbox" htmlFor={`key${headerText}`}>
        <input
          type="checkbox"
          checked={checked}
          onChange={(e) => this.handleToggleKey(e, column)}
          id={`key${headerText}`}
        />
        {headerText}
      </label>
    );
  }

  dataGrid() {
    const { table } = this.props;

    const columns = table.columns.map((column) => ({
      key: column.column,
      name: column.column,
      resizable: true,
      headerRenderer: (args) => this.headerRenderer(args, column),
      hash: (new Date()).getTime(),
    }));

    const { rows } = table;
    return (
      <div className="grid-container">
        <div className="absoluteWrapper">
          <ReactDataGrid
            columns={columns}
            rowGetter={(i) => rows[i]}
            rowsCount={rows.length}
          />
        </div>
      </div>
    );
  }

  static renderButton(buttonInfo: ButtonInfo) {
    const { title, callback, classes, autoFocus } = buttonInfo;
    const buttonClasses = classNames(classes || []);

    return (
      <button
        type="button"
        onClick={callback}
        className={buttonClasses}
        autoFocus={autoFocus}
      >
        {title}
      </button>
    );
  }

  render() {
    const { table, buttons } = this.props;
    const { headers } = this.state;

    return (
      <div className="table-preview">
        <h2 className="title is-3">{table.name}</h2>
        <h3 className="subtitle is-5">
          The headers are &nbsp;
          <span className="tag is-success">{headers ? 'on' : 'off' }</span>
        </h3>
        <div className="actions">
          {buttons.map(TablePreview.renderButton)}
        </div>
        <section className="grid-section">
          {this.dataGrid()}
          {this.sheetTabs()}
        </section>
      </div>
    );
  }
}

export default TablePreview;
