import {
  applyMiddleware, createStore, combineReducers, compose,
} from 'redux';
import createSagaMiddleware from 'redux-saga';
import { connectRouter, routerMiddleware } from 'connected-react-router';
import { createLogger } from 'redux-logger';

// Reducer that does nothing
const noopReducer = (state = {}) => state;

//
// Configures new redux store.
//
export default (context, initialState = {}) => {
  // Init redux-saga
  const sagaMiddleware = createSagaMiddleware();

  // List of default middlewares
  const middlewares = [
    sagaMiddleware,

    // Init react-router-redux
    routerMiddleware(context.history),
  ];

  // Log actions in dev mode
  if (process.env.NODE_ENV === 'development') {
    middlewares.push(createLogger());
  }

  const w = window;
  const composeEnhancers = w.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;

  // Create store
  const store = createStore(
    noopReducer,
    initialState,
    composeEnhancers(applyMiddleware(...middlewares)),
  );

  // Hash with registered reducers
  const reducers = { };

  // Dynamically adds new reducer to the store
  store.injectReducer = (name, reducer) => {
    // Register reducer inside registrey
    reducers[name] = reducer;

    // NB: connected-react-router API sucks ass
    // Compbine all registered reducers with connected-react-router reducer
    const rootReducer = combineReducers({
      router: connectRouter(context.history),
      ...reducers,
    });

    // Replace reducer
    store.replaceReducer(rootReducer);
  };

  // Dynamically runs new saga for the store
  store.runSaga = (saga) => {
    sagaMiddleware.run(saga);
  };

  return store;
};
